import Mescroll  from 'mescroll.js'
import 'mescroll.js/mescroll.min.css'
import { selectService } from "../../server/employee";
export default {
  name: "selectService",
  components:{Mescroll },
  data() {
    return {
      imgUrl: require("@/assets/img/seach.png"),
      value: "",
      count: 0,
      mescroll:null,
      page: {
        num: 1,
        size: 50,
        total: 0
      },
      dataList: [] // 列表数据
    };
  },
 created() {
  },
  mounted(){
    this.mescroll=new Mescroll(this.$refs.mescroll,{
      // down:{
      //   callback:this.downCallback
      // },
      up:{
        user:true,
        callback:this.getList,
        noMoreSize:5,
        htmlNodata:'<p class="upwarp-nodata">暂无更多数据---</p>'
      },
      empty: {
      warpId: "mescroll", //
      icon: "./static/mescroll/mescroll-empty.png", //
      tip: "暂无相关数据~" //提示
    }
    })
    up:{
      user:true;
    }
  },
  methods: {
   getList(page) {
    const that = this
      selectService({
        page: this.page.num,
        limit: this.page.size,
        organizationName:this.value
      }).then(e => {
        if (e.data.code == 200) {
          if (this.page.num == 1) {
            that.dataList = [];
          }
          var datas = e.data.data;
          var data = datas.list;
          if (data && data.length > 0) {
            that.dataList = that.dataList.concat(data);
          }
        }
        that.$nextTick(() => {
          that.mescroll.endByPage(data.length, e.data.data.total)
        });
      });
    },
    handleSeach(){
      this.page.page = 1;
      this.getList();
    },
    handleLink(id,organizationName){
      console.log(organizationName);
      localStorage.setItem('id',id);
      localStorage.setItem('organizationName',organizationName);

      // sessionStorage.setItem('id',id);
      this.$router.push({
        path:'/AddEmployee',
        query:{
          id:id,
          name:organizationName
        }
      })
    }
  }
};



